.page {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.stars {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1, .stars--2, .stars--3, .stars--4 {
  background-image: url("star-active.edda4c43.svg");
}

.stars--5 {
  background-image: url("star.822d561e.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__img {
  box-sizing: border-box;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 5px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 22px;
  display: flex;
}

.card__reviews_stars {
  display: flex;
}

.card__reviews_count {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price_title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price_sum:after {
  content: "$2,199 ";
  color: #060b35;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}
